.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 17px;
}

.header__contacts {
	display: flex;
	justify-content: flex-end;
	margin-right: 15px;
	margin-top: -33px;
}

.header__link {
	display: block;
	padding-left: 23px;
	margin: 0;
}

.header__callback {
	display: none;
}

.header__email,
.header__phone {
	display: none;
}

@media (min-width: $tablet-width) {
  .header {
    padding-top: 17px;
  }
  
	.logo__img {
		height: 52px;
		width: 52px;
  }
  
	.header__contacts {
		margin-right: 23px;
		margin-top: -54px;
  }

  .header__callback {
    display: none;
  }
  
	.header__link {
		padding-left: 34px;
	}
}

@media (min-width: $desktop-width) {
  .header {
    padding-top: 8px;
  }

	.header__email,
	.header__phone {
    font-family: "PT Mono", "Arial", sans-serif;
    // color: #ffffff;
		font-weight: 400;
		font-size: 16px;
		line-height: 18px;
    display: block;
  }

  .header__icon {
    display: block;
    margin-right: 14px;
    margin-top: 2px;
  }

  .header__contacts {
		margin-right: 35px;
    margin-top: -62px;
  }
  
  .header__link {
		display: flex;
  }
  
  .header__icon--email {
    width: 19px;
    height: 14px;
  }
  
  .header__icon--phone {
    width: 18px;
    height: 18px;
  }

  .header__callback {
    display: block;
    // color: #ffffff;
    text-transform: uppercase;
    text-align: center;
    font-family: "Roboto Condensed","Arial",sans-serif;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    width: 180px;
    height: 45px;
    border: 1px solid;
    border-radius: 5px;
    margin: -8px 0 0 50px;
    box-sizing: border-box;
    padding-top: 12px;
  }
    
}
